import styled from 'styled-components';
import { Container, CloseBtn } from '../../style';

export const CloseButton = styled(CloseBtn)`
    color: #dfdfdf;
    font-size: 24px;
    margin-left: 50px;
    position: absolute;
    right: 25px;
    top: 5px;
`;
type ModalType = {
    type: string;
};
export const ModalContainer = styled.div`
    @media only screen and (min-device-width: 375px) and (max-device-width: 812px) and (-webkit-device-pixel-ratio: 3) {
        top: 0;
        left: 0;
        width: auto;
        min-height: 95%;
    }
    @media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
        top: 0;
        left: 0;
        width: auto;
        min-height: 95%;
    }
    position: relative;
    width: auto;
    margin: 0.5rem;
    pointer-events: none;
    .modal-content {
        position: relative;
        display: flex;
        flex-direction: column;
        width: 100%;
        pointer-events: auto;
        background-color: #fff;
        background-clip: padding-box;
        border: 1px solid rgba(0, 0, 0, 0.2);
        border-radius: 0.3rem;
        outline: 0;
        margin: auto;
        max-width: ${(props: ModalType) => (props.type === 'signUp' ? '900px' : '500px')};
    }
    .modal-header {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        padding: ${(props: ModalType) => (props.type === 'signUp' ? '0' : '1rem 1rem')};
        border: none;
    }
`;
