import React, { ReactNode } from 'react';
import { Modal } from '@material-ui/core';
import { ModalContainer, CloseButton } from './style';

type ModalType = {
    children: ReactNode;
    handleClose: any;
    open: boolean;
    type: string;
};

function ModalComponent(props: ModalType) {
    const { handleClose, open, children, type } = props;

    return (
        <Modal
            open={open}
            onClose={handleClose}
            aria-labelledby="simple-modal-title"
            aria-describedby="simple-modal-description"
        >
            <ModalContainer type={type}>
                <div className="modal-content">
                    <div className="modal-header"><CloseButton onClick={handleClose}>x</CloseButton></div>
                    {children}
                </div>
            </ModalContainer>
        </Modal>
    );
}

// ModalComponent.prototype = propTypes;
export default ModalComponent;
